import Head from 'next/head';

// This component preloads critical images to improve LCP
export default function PreloadImages() {
  const criticalImages = [
    '/RPTracker/RPTLogo.png',
    '/SAES/SAESImg.png',
  ];

  return (
    <Head>
      {criticalImages.map((src, index) => (
        <link
          key={index}
          rel="preload"
          href={src}
          as="image"
          type="image/png"
        />
      ))}
    </Head>
  );
}